<template>
  <div id='httpproxy'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/httpproxy'}"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span='10'>
        <el-input v-model="searchForm.search_value" placeholder=""  @keyup.enter.native="searchData"></el-input>
      </el-col>
      <el-col :span="6" style="font-size: 14px">
        <el-popover ref="searchHelp" placement="top-start" title="" width="400" trigger="hover" open-delay=1000>
          <div style="font-size: 10px">
          <p>1. (DOMAIN)(APP)(APP_PORT)(LISTEN_PORT)(SERVER)(USER)(DESC)</p>
          <p>2. DOMAIN:<>&APP:<>... </p>
          <p>3. DOMAIN=<>&APP=<>... </p>
          <p>4. </p>
          </div>
        </el-popover>
        <el-button @click="searchData" type="primary" icon="el-icon-search" v-popover:searchHelp> </el-button>
      </el-col>
      <el-col :span="8" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round></el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="right" inline class="demo-table-expand">
            <el-form-item label="">
              <span>{{ props.row.proxy_domain_name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.app_name }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.app_port }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.proxy_listen_ports }}</span>
            </el-form-item>
            <el-form-item label="">
              <span v-for="server in props.row.app_servers.split('/')">
                <p style='line-height: 10px'>{{ server }}</p>
              </span>
            </el-form-item>
            <el-form-item label="">
              <span v-for="server in props.row.app_servers_backup.split('/')">
                <p style='line-height: 10px'>{{ server }}</p>
              </span>
            </el-form-item>
            <el-form-item label="">
              <span v-for="dn in props.row.other_domain_names.split('/')">
                <p style='line-height: 10px'>{{ dn }}</p>
              </span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.description }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.add_time }}</span>
            </el-form-item>
            <el-form-item label="">
              <span>{{ props.row.add_user }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="id"    label="ID" width=50></el-table-column>
      <el-table-column prop="proxy_domain_name"    label="" width=200></el-table-column>
      <el-table-column prop="app_name"      label=""></el-table-column>
      <el-table-column prop="app_port"    label=""></el-table-column>
      <el-table-column prop="status"      label="">
        <template slot-scope="scope">
          <span :style="{color: scope.row.status[1]}">{{ scope.row.status[0] }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="operation"     label="">
        <template slot-scope="scope">
          <el-tooltip content="" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top" v-if="scope.row.status[1]!='gray'" >
            <i class="fa fa-pause" @click="handledisable(scope.row)"></i>
          </el-tooltip>
          <el-tooltip content="" placement="top" v-if="scope.row.status[1]=='gray'" >
            <i class="fa fa-play" @click="handlenable(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top">
            <i class="fa fa-repeat" @click="repush(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top">
            <i class="fa fa-file-o" @click="showLog(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="" :visible.sync="dialogVisible">
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item v-show="false">
          <el-input v-model="formData.id"></el-input>
        </el-form-item>
        <el-form-item label="" prop="proxy_domain_name" :rules="[{ required: true, message: ''}]">
          <el-input v-model="formData.proxy_domain_name"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="app_name" :rules="[{ required: true, message: ''}]">
          <el-input v-model="formData.app_name"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="app_port" :rules="[{ required: true, message: ''}]">
          <el-input v-model="formData.app_port" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="app_servers" :rules="[{ required: true, message: 'IP'}]">
          <el-input v-model="formData.app_servers" placeholder="IP'/'"></el-input>
        </el-form-item>
        <el-form-item label="" prop="app_servers_backup">
          <el-input v-model="formData.app_servers_backup" placeholder="'/'"></el-input>
        </el-form-item>
        <el-form-item label="" prop="other_domain_names">
          <el-input v-model="formData.other_domain_names" placeholder="'/'"></el-input>
        </el-form-item>
        <el-form-item label="" prop="proxy_listen_ports">
          <el-input v-model="formData.proxy_listen_ports" placeholder="80'/'"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" :rows="4" v-model="formData.description" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'"> </el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'"> </el-button>
      </div>
    </el-dialog>

    <el-dialog :title="popUpTitle" :visible.sync="popUpDialog">
      <el-input auto-complete="off" type="textarea" :rows="22" readonly='readonly' v-model="logContent.logs"></el-input>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      mysearch: '',
      searchForm: {
        search_value: null,
      },
      formData: {
        id:  null,
        proxy_domain_name: null,
        app_name: null,
        app_port: null,
        app_servers:null,
        app_servers_backup: null,
        other_domain_names: null,
        proxy_listen_ports: null,
        description: null,
      },
      action: 'add',
      dialogVisible: false,
      popUpDialog: false,
      popUpTitle: null,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/httpproxy',
        method: 'post',
        data: null,
      },
      logContent: {
        logs: null,
      },
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // jquery.param
        paramSerializerJQLikeEnabled: true,
        url: '/httpproxy',
        // to methods of that instance.
        method: 'get',
        // paramsget url
        params: params
      };
      service.request(config).then(data => {
          // console.log(data);
          this.tableData = data.data;
          let refresh = 0;
          for(let obj of this.tableData){
            if(obj.status[1] == 'orange'){
              refresh = 1;
              break;
            }
          };
          if(refresh == 1) {
            setTimeout(() => {this.getData()},10000);
          };
        }, err => {
        this.$alert(err.message);
        });
     },

     searchData () {
       let params = {search_value: this.searchForm.search_value};
       let config = {
         baseURL: '/proxy/api/v1',
         paramSerializerJQLikeEnabled: true,
         url: '/httpproxy',
         method: 'get',
         params: params
       };
       service.request(config).then(data => {
         // console.log(data);
         this.tableData = data.data;
         }, err => {
         this.$alert(err.message);
         });
      },

    resetForm () {
      this.formData = {
        id:  null,
        proxy_domain_name: null,
        app_name: null,
        app_port: null,
        app_servers:null,
        app_servers_backup: null,
        other_domain_names: null,
        proxy_listen_ports: null,
        description: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleEdit(row){
      this.resetForm();
      this.action = 'edit';
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.dialogVisible = true;
    },
    confirmEdit () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    warningAndAction(row, confirmMessage, action, successMessage) {
      this.$confirm(confirmMessage,"",{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {id: row.id, action: action};
        service.request(this.postConfig).then(data => {
          this.$message({message: successMessage, type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    handleDelete(row){
        let confirmMessage = `${row.proxy_domain_name}`;
        let action = 'delete';
        let successMessage = '';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handledisable(row){
        let confirmMessage = `${row.proxy_domain_name}`;
        let action = 'disable';
        let successMessage = '';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handlenable(row){
        let confirmMessage = `${row.proxy_domain_name}`;
        let action = 'enable';
        let successMessage = '';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    repush(row){
        let confirmMessage = `${row.proxy_domain_name}`;
        let action = 'saltRepush';
        let successMessage = '';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    showLog(row){
        this.postConfig.data = {id: row.id, action: 'getPushLog'};
        service.request(this.postConfig).then(data => {
          this.logContent.logs = data.logs;
          this.popUpTitle = `${row.proxy_domain_name}saltstack`
          this.popUpDialog = true;
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
    },

  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



