import HttpProxy   from '../views/http_proxy.vue'
import TcpProxy    from '../views/tcp_proxy.vue'
//import FlowControl from '../views/flow_control.vue'
import MainPage    from '../views/main.vue'
import ClusterSetting from '../views/cluster_setting.vue'
import Users from '../views/users.vue'
import WhiteList from '../views/whitelist.vue'
import IPList from '../views/iplist.vue'
import BlackList from '../views/blacklist.vue'

const routes = [
    { path: '/',
       component: MainPage,
        children: [
            { path: 'httpproxy', component: HttpProxy },
            { path: 'tcpproxy', component: TcpProxy },
            //{ path: 'flowcontrol', component: FlowControl },
            { path: 'clustersetting', component: ClusterSetting },
            { path: 'users', component: Users },
            { path: 'whitelist', component: WhiteList },
            { path: 'blacklist', component: BlackList },
            { path: 'iplist', component: IPList },
        ],
      redirect: '/httpproxy'
    },
]

export default routes;



