<template>
  <div id='clustersetting'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/clustersetting'}"></el-breadcrumb-item>
      <el-breadcrumb-item>NLB</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>

    <el-main>
    <el-row :gutter='8'>
      <el-col :span="12" style="font-size: 14px; text-align: left;">
        <el-button @click='handleInit' round></el-button>
      </el-col>
      <el-col :span="12" style="font-size: 14px; text-align: right;">
        <el-button type="success" @click='handleExp' round></el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="hostname" label="" width=200></el-table-column>
      <el-table-column prop="role"     label=""></el-table-column>
      <el-table-column prop="ip"       label="IP"></el-table-column>
      <el-table-column prop="status" label="">
        <template slot-scope="scope">
          <span :style="{color: scope.row.status[1]}">{{ scope.row.status[0] }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="operation"     label="">
        <template slot-scope="scope">
          <!--
          <el-tooltip content="" placement="top">
            <i class="fa el-icon-caret-top" @click="handleSetMaster(scope.row)" v-if="scope.row.role=='MINION'"></i>
            <i class="fa el-icon-caret-bottom" @click="handleSetMinion(scope.row)" v-if="scope.row.role=='MASTER'"></i>
          </el-tooltip>
          <span>|</span> -->
          <el-tooltip content="" placement="top">
            <i class="fa el-icon-delete" @click="hanldeKickoff(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="NLB" :visible.sync="dialogClusterExp">
      <span></span>
      <div style="font-size: 12px; line-height: 0.8;">
        <p >1.IP</p>
        <p >2.</p>
        <p >3.</p>
      </div>
      <el-form :model="clusterExpForm" ref="clusterExpForm">
        <el-form-item label="IP" prop="ip_list">
          <el-input type="textarea" :rows="8" v-model="clusterExpForm.ip_list" placeholder="IP"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogClusterExp = false"> </el-button>
        <el-button type="primary" @click="confirmExp"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogInit">
      <span></span>
      <div style="font-size: 12px; line-height:1.1">
      <p>1.</p>
      <p>2.saltstackyumyumsaltstack</p>
      <p>4.</p>
      <p>3.<b style='color: red'></b>ssh/</p>
      </div>
      <el-form :model="initSettingForm" ref="initSettingForm">
        <el-form-item label="" prop="sysuser" label-width="110px" :rules="[{ required: true, message: 'ID'}]">
          <el-input v-model="initSettingForm.sysuser" placeholder="sshID"></el-input>
        </el-form-item>
        <el-form-item label="" prop="syspasswd" label-width="110px" :rules="[{ required: true, message: ''}]">
          <el-input v-model="initSettingForm.syspasswd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sethostname" label-width="110px">
          <el-select v-model="initSettingForm.sethostname" placeholder="" style="width:100%;">
            <el-option v-for="item in sethostnameOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="hostnameprefix" label-width="110px">
          <el-input v-model="initSettingForm.hostnameprefix" placeholder="<>-<>"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogInit = false"> </el-button>
        <el-button type="primary" @click="confirmInitSet"> </el-button>
      </div>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      clusterExpForm: {
        ip_list: null,
      },
      initSettingForm:{
        sysuser: null,
        syspasswd: null,
        sethostname: null,
        hostnameprefix: 'NLB-CLUSTER'
      },
      sethostnameOptions: [{value: 'Y', label: ''}, {value: 'N', label: ''}],
      dialogClusterExp: false,
      dialogInit: false,
      initSettingEditable: false,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/cluster',
        method: 'post',
        data: null,
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let config = {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/cluster',
        method: 'get',
        params: {},
      };
      service.request(config).then(data => {
        this.tableData = data.data;
        }, err => {
        this.$alert(err.message);
        });
     },

    handleExp () {
      this.clusterExpForm = {ip_list: null};
      this.dialogClusterExp = true;
    },
    confirmExp () {
      this.postConfig.data = this.clusterExpForm;
      this.postConfig.data.action = 'expandCluster';
      service.request(this.postConfig)
      .then(data => {
        this.$message({message: '', type: 'success'});
        this.dialogClusterExp = false;
        this.getData();
      }, err => {
        this.$message({message: err.message, type: 'error'});
      });
    },

    getInitConfig() {
      this.postConfig.data = {'action':'getInit'};
      service.request(this.postConfig).then(data => {
        this.initSettingForm = data.initSetting;
      }, err =>{this.alert(err.message);});
    },
    handleInit() {
      this.dialogInit = true;
      this.getInitConfig();
    },
    confirmInitSet() {
      this.postConfig.data = this.initSettingForm;
      this.postConfig.data.action = 'setInit';
      service.request(this.postConfig).then(data => {
        this.$message({message: '',type: 'success'});
        this.dialogInit = false;
      }, err => {
        this.$message({message: err.message, type: 'error'});
      });
    },

    hanldeKickoff(row) {
      this.$confirm(`ip: ${row.ip}`,"",{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'kickoffNode'};
        service.request(this.postConfig).then(data => {
          this.$message({message: '', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    /* handleSetMaster(row) {
      this.$confirm(`(ip: ${row.ip})master`,"",{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'setMaster'};
        service.request(this.postConfig).then(data => {
          this.$message({message: 'master', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },
    handleSetMinion(row) {
      this.$confirm(`(ip: ${row.ip})minion`,"",{
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'setMinion'};
        service.request(this.postConfig).then(data => {
          this.$message({message: 'master', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    }, */

  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



