<template>
  <el-container style="border: 1px solid #eee">

    <el-aside :width='asidewidth'>
    <el-menu :default-active="$route.path" class="el-menu-vertical-demo" :collapse="isCollapse" :router='true'>
    <img src='/static/img/1111.png' height='60px' width='200px' style='background-color: #4C87BC;'></img>
      <el-submenu index="1">
        <template slot="title"><i class="fa fa-sitemap"></i><span> </span></template>
        <el-menu-item index="/httpproxy"></el-menu-item>
        <el-menu-item index="/tcpproxy"></el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title"><i class="fa fa-exchange"></i><span> </span></template>
        <el-menu-item index="/iplist">IP</el-menu-item>
        <el-menu-item index="/whitelist"></el-menu-item>
        <el-menu-item index="/blacklist"></el-menu-item>
        <!--
        <el-menu-item index="/tokenbucket"></el-menu-item>
        -->
      </el-submenu>
      <!--
      <el-submenu index="3">
        <template slot="title"><i class="fa fa-cubes"></i><span> </span></template>
        <el-menu-item index="/flowcontrol"></el-menu-item>
        <el-menu-item index="/flowcontrol"></el-menu-item>
        <el-menu-item index="/whitelist"></el-menu-item>
        <el-menu-item index="/blacklist"></el-menu-item>
      </el-submenu>
      -->
      <el-submenu index="3">
        <template slot="title"><i class="fa fa-gears"></i><span> </span></template>
        <el-menu-item index="/clustersetting">NLB</el-menu-item>
      </el-submenu>
      <el-menu-item index="/users">
        <i class="fa fa-users"></i>
        <span slot="title"></span>
      </el-menu-item>
    </el-menu>
    </el-aside>

    <el-container>
      <el-header>
      <el-row>
        <el-col :span='6'>
          <i class="fa fa-navicon" @click='asideCollapse'></i>
        </el-col>
        <el-col :span='18' style="text-align: right; font-size: 16px;">
          <span>{{ currentUser }} </span>

          <el-dropdown @command="handleCommand" trigger="click">
            <span class="el-dropdown-link" style='color: #fff;'>
              <i class="el-icon-setting"></i>
            </span>
            <el-dropdown-menu slot="dropdown" >
              <el-dropdown-item command="changePasswd"></el-dropdown-item>
              <el-dropdown-item command="logout"></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>

        </el-col>
      </el-row>

      <el-dialog title="" :visible.sync="popUpDialog">
        <el-form :model="passwdData" ref="passwdData" label-width="100px">
          <el-form-item label="" prop="old_password" :rules="[{ required: true, message: ''}]">
            <el-input v-model="passwdData.old_password"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="new_password" :rules="[{ required: true, message: ''}]">
            <el-input v-model="passwdData.new_password"  placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="confirm_password" :rules="[{ required: true, message: ''}]">
            <el-input v-model="passwdData.confirm_password"  placeholder=""></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="popUpDialog = false"> </el-button>
          <el-button type="primary" @click="formSubmit"> </el-button>
        </div>
      </el-dialog>

      </el-header>

      <router-view></router-view>

    </el-container>
  </el-container>
</template>

<script>
import DataProvider from '@bbfe/data-provider';
let service = new DataProvider();

export default {
  data() {
    return {
      isCollapse: false,
      asidewidth: '200px',
      currentUser: '',
      popUpDialog: false,
      passwdData: {
        old_password: null,
        new_password: null,
        confirm_password: null,
      },
    }
  },
  mounted(){
    this.getUserInfo();
  },
  methods: {
    getUserInfo() {
      let config = {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/userinfo',
        method: 'get',
      };
      service.request(config)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.currentUser = data.realname;
        }
      }, err => {
        this.currentUser = '';
      });
    },
    asideCollapse() {
      if (this.isCollapse) {
        this.isCollapse = false;
        this.asidewidth = '200px';
      } else {
        this.isCollapse = true;
        this.asidewidth = '64px';
      }
    },
    handleCommand(command) {
      if (command == "logout"){
        window.location.href="/logout";
      } else if (command == "changePasswd") {
        this.resetForm();
        this.popUpDialog = true;
      }
    },

    resetForm(){
      Object.keys(this.passwdData).forEach(k => {
        this.passwdData[k] = null;
      });
    },
    formSubmit(){
      let data = this.passwdData;
      data.action = "changePasswd";
      let config = {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/httpproxy',
        method: 'post',
        data: data
      };
      service.request(config)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.popUpDialog = false;
        } else {
          this.$message({
            message: '' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },
  }

}
</script>

<style>
.el-header {
  //background-color: #3c8dbc;
  background-color: #4C87BC;
  color: #fff;
  line-height: 60px;
}

.el-menu {
}

.el-menu--collapse {
  width: 64px;
}
</style>



