<template>
  <div id='users'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span="24" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round></el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="username"    label="ID"></el-table-column>
      <el-table-column prop="realname"    label=""></el-table-column>
      <el-table-column prop="email"    label=""></el-table-column>
      <el-table-column prop="group"      label=""></el-table-column>
      <el-table-column prop="operation"     label="">
        <template slot-scope="scope">
          <el-tooltip content="" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top">
            <i class="el-icon-view"   @click="handleResetPW(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="readableAction" :visible.sync="dialogVisible">
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item label="ID" prop="username" :rules="[{ required: true, message: 'ID'}]">
          <el-input v-model="formData.username"  placeholder="ID" :readonly='usernameReadOnly'></el-input>
        </el-form-item>
        <el-form-item label=" " prop="group" :rules="[{ required: true, message: ''}]">
          <el-select v-model="formData.group" placeholder="" style='width: 100%'>
            <el-option v-for="item in groupOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="passwd" :rules="[{ required: true, message: ''}]" v-if="this.action == 'add'">
          <el-input v-model="formData.passwd"  placeholder="8"></el-input>
        </el-form-item>
        <el-form-item label=" " prop="realname">
          <el-input v-model="formData.realname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" " prop="email">
          <el-input v-model="formData.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'"> </el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'"> </el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="resetPWVisible">
      <el-form :model="formResetPW" ref="formResetPW" label-width="100px">
        <el-form-item label="ID" prop="username" :rules="[{ required: true, message: 'ID'}]">
          <el-input v-model="formResetPW.username"  placeholder="ID" readonly></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPasswd" :rules="[{ required: true, message: ''}]">
          <el-input v-model="formResetPW.newPasswd"  placeholder="8"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetPWVisible = false"> </el-button>
        <el-button type="primary" @click="confirmResetPW"> </el-button>
      </div>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      formData: {
        username: null,
        passwd: null,
        realname: null,
        email: null,
        group: null,
      },
      formResetPW:{
        username: null,
        newPasswd: null,
      },
      action: null,
      dialogVisible: false,
      groupOptions: null,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/users',
        method: 'post',
        data: null,
      },
      readableAction: '',
      resetPWVisible: false,
      usernameReadOnly: false,
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // apibaseURL, DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // jquery.param
        paramSerializerJQLikeEnabled: true,
        url: '/users',
        // to methods of that instance.
        method: 'get',
        // paramsget url
        params: params
      };
      service.request(config).then(data => {
        // console.log(data);
        this.tableData = data.data;
        this.groupOptions = data.groups
        }, err => {
        this.$alert(err.message);
        });
     },

    resetForm () {
      this.formData = {
        username: null,
        passwd: null,
        realname: null,
        email: null,
        group: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.readableAction = '';
      this.usernameReadOnly = false;
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleDelete (row) {
          this.$confirm(`${row.username} ?`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            this.postConfig.data = {username: row.username, action: 'delete'};
            service.request(this.postConfig)
            .then(data => {
              if (data.result == 'SUCCESS') {
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.getData();
              } else {
                this.$message({
                  message: data.message,
                  type: 'error'
                });
              }
            }, err => {
              this.$alert(err.message);
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: ''
            });
          });
    },

    handleEdit(row){
      this.resetForm();
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.action = 'edit';
      this.readableAction= '';
      this.usernameReadOnly = true;
      this.dialogVisible = true;
    },
    confirmEdit () {
    this.postConfig.data = this.formData;
    this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleResetPW(row){
      this.formResetPW.username = row.username;
      this.formResetPW.newPasswd = null;
      this.action = 'resetPasswd';
      this.resetPWVisible = true;
    },
    confirmResetPW() {
      this.postConfig.data = this.formResetPW;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '',
            type: 'success'
          });
          this.resetPWVisible = false;
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },


  }
}
</script>

<style>
</style>



